<template>
    <div class="task" v-loading="loading">
        <el-row id="top-buttons">
            <el-button-group>
                <el-button type="primary" v-on:click="createTask">
                    发起任务
                    <i class="el-icon-s-promotion"></i>
                </el-button>
            </el-button-group>
        </el-row>
        <el-card class="box-card">
            <el-table :data="task_data" style="width: 100%">
                <el-table-column
                    prop="task_id"
                    label="任务ID">
                </el-table-column>
                <el-table-column
                    prop="party_name"
                    label="发起方">
                </el-table-column>
                <el-table-column
                    prop="party_names"
                    label="参与方">
                </el-table-column>
                <el-table-column
                    label="状态">
                    <template slot-scope="scope">
                        <span v-bind:class="scope.row.status">{{ scope.row.status | task_status }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="create_time"
                    label="任务创建时间">
                </el-table-column>
                <el-table-column
                    prop="update_time"
                    label="状态更新时间">
                </el-table-column>
                <el-table-column
                    label="备注">
                    <template slot-scope="scope">
                        <el-input
                            type="textarea"
                            autosize
                            v-model="scope.row.alias"
                            placeholder="暂无"
                            @change="changeAlias(scope.row)">
                        </el-input>
                    </template>
                </el-table-column>
                <el-table-column
                    label="操作">
                    <template slot-scope="scope">
                        <el-button
                            type="primary"
                            size="small"
                            class="oper-btn"
                            v-on:click="toDetail(scope.row.task_id)" >
                            任务详情
                        </el-button>
                        <el-button
                            type="info"
                            size="small"
                            class="oper-btn"
                            v-on:click="downloadLog(scope.row.task_id)">
                            下载日志
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                id="pagination"
                background
                :page-size="page_size"
                :page-count="page_count"
                :total="total_count"
                layout="prev, pager, next, jumper"
                @current-change="handleCurrentChange"
                >
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                // 数据：任务数据
                task_data: [],
                // 标识：是否展示"加载中"动画
                loading: false,
                // 数据：每页显示的 task 数量
                page_size: 10,
                page_count: 1,
                total_count: 1,
            }
        },
        mounted() {
            if (window.localStorage.getItem('jeddak_token') !== null) {
                this.getTaskData(1)
            }
        },
        methods: {
            // 获取任务数据
            getTaskData(curr_page) {
                this.loading = true;
                let payload = new URLSearchParams({
                    req_type: 'info',
                    filter: JSON.stringify({
                        'deleted': false,
                    }),
                    page_size: this.page_size,
                    curr_page: curr_page,
                    order_by: '-create_time'
                })
                this.$axios.get("/board/tasks?"+payload.toString(),{
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                })
                .then(resp => {
                    this.task_data = resp.data.data.tasks;
                    for (let i = 0; i < this.task_data.length; i++) {
                        this.task_data[i].create_time = new Date(this.task_data[i].create_time+'Z').toLocaleString();
                        this.task_data[i].update_time = new Date(this.task_data[i].update_time+'Z').toLocaleString();
                    }
                    this.total_count = resp.data.data.total_count;
                    this.page_count = Math.ceil(this.total_count/this.page_size);
                    this.loading = false;
                }).catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '提示',
                        message: '操作失败，可能不具备权限，请尝试注销重新登录或联系管理员'
                    });
                    this.loading = false;
                });
            },
            // 下载日志文件
            downloadLog(task_id) {
                const url = "/board/task/"+task_id+"?type=log";
                const file_name = task_id + ".log"
                this.$axios({
                    url:url,
                    method:'get',
                    responseType: 'blob',
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                }).then(res => {
                    let blob = new Blob([res.data]);
                    if (window.navigator.msSaveOrOpenBlob) {
                        navigator.msSaveBlob(blob, file_name);
                    } else {
                        let link = document.createElement("a");
                        let evt = document.createEvent("HTMLEvents");
                        evt.initEvent("click", false, false);
                        link.href = URL.createObjectURL(blob);
                        link.download = file_name;
                        link.style.display = "none";
                        document.body.appendChild(link);
                        link.click();
                        window.URL.revokeObjectURL(link.href);
                    }
                });
            },
            createTask() {
                this.$router.push("/task_new");
            },
            // 跳转到任务详情页
            toDetail(task_id) {
                this.$router.push("/task_detail?task_id="+task_id);
                // let routeData = this.$router.resolve(
                //     {
                //         path: '/task_detail',
                //         query: {'task_id': task_id}
                //     });
                // window.open(routeData.href, '_blank');
            },
            // 分页跳转
            handleCurrentChange(val) {
                this.getTaskData(val);
            },

            // 修改备注
            changeAlias(row) {
                this.$axios.patch("/board/task/"+row.task_id, {
                    'field': {
                        'alias': row.alias
                    },
                }, {
                    headers: {
                        'auth': window.localStorage.getItem('jeddak_token')
                    }
                })
                // eslint-disable-next-line no-unused-vars
                .then(resp => {
                    this.$notify.success({
                        title: '提示',
                        message: '修改成功'
                    });
                }).catch(err => {
                    console.log(err);
                    this.$notify.error({
                        title: '提示',
                        message: '操作失败，可能不具备权限，请尝试注销重新登录或联系管理员'
                    });
                });
            }
        },
    }
</script>

<style scoped>
    #pagination {
        margin-top: 20px;
        float: right;
        margin-bottom: 20px;
    }
    .oper-btn {
        margin-left: 0;
        margin-right: 5px;
    }
</style>
